import Transformable from '../core/Transformable';
import Matrix from '../core/Matrix';
import Rectangle from '../core/shape/Rectangle';
import ObservableVector from '../core/ObservableVector';

/**
 * @class Renderable
 * @extends {Transformable}
 */
export default class Renderable extends Transformable {

    /**
     * @constructor
     */
    constructor() {
        super();

        /**
         * @private
         * @member {Boolean}
         */
        this._visible = true;

        /**
         * @private
         * @member {ObservableVector}
         */
        this._size = new ObservableVector(this._updateSize, this);

        /**
         * @private
         * @member {Matrix}
         */
        this._worldTransform = new Matrix();

        /**
         * @private
         * @member {?Rectangle}
         */
        this._localBounds = null;

        /**
         * @private
         * @member {?Rectangle}
         */
        this._bounds = null;
    }

    /**
     * @public
     * @member {Boolean}
     */
    get visible() {
        return this._visible;
    }

    set visible(visible) {
        this._visible = visible;
    }

    /**
     * @public
     * @member {ObservableVector}
     */
    get size() {
        return this._size;
    }

    set size(size) {
        this._size.copy(size);
    }

    /**
     * @public
     * @member {Number}
     */
    get width() {
        return this._size.x;
    }

    set width(width) {
        this._size.x = width;
    }

    /**
     * @public
     * @member {Number}
     */
    get height() {
        return this._size.y;
    }

    set height(height) {
        this._size.y = height;
    }

    /**
     * @public
     * @readonly
     * @member {Number}
     */
    get left() {
        return (this.x - this.width + this.origin.x);
    }

    /**
     * @public
     * @readonly
     * @member {Number}
     */
    get top() {
        return (this.y - this.height + this.origin.y);
    }

    /**
     * @public
     * @readonly
     * @member {Number}
     */
    get right() {
        return (this.x + this.width + this.origin.x);
    }

    /**
     * @public
     * @readonly
     * @member {Number}
     */
    get bottom() {
        return (this.y + this.height + this.origin.y);
    }

    /**
     * @public
     * @readonly
     * @member {Rectangle}
     */
    get localBounds() {
        return this.getLocalBounds();
    }

    /**
     * @public
     * @readonly
     * @member {Rectangle}
     */
    get bounds() {
        return this.getBounds();
    }

    /**
     * @public
     * @member {Matrix}
     */
    get worldTransform() {
        return this._worldTransform;
    }

    set worldTransform(worldTransform) {
        this._worldTransform.copy(worldTransform);
    }

    /**
     * @public
     * @chainable
     * @param {Number} width
     * @param {Number} height
     * @returns {Renderable}
     */
    setSize(width, height) {
        this._size.set(width, height);

        return this;
    }

    /**
     * @public
     * @returns {Rectangle}
     */
    getLocalBounds() {
        if (!this._localBounds) {
            this._localBounds = new Rectangle();
        }

        return this._localBounds.set(0, 0, this.width, this.height);
    }

    /**
     * @public
     * @returns {Rectangle}
     */
    getBounds() {
        if (!this._bounds) {
            this._bounds = new Rectangle();
        }

        return this._bounds
            .copy(this.getLocalBounds())
            .transform(this.getTransform());
    }

    /**
     * @public
     * @virtual
     * @chainable
     * @param {DisplayManager} renderManager
     * @param {Matrix} worldTransform
     * @returns {Renderable}
     */
    render(renderManager, worldTransform) {
        if (!this.visible) {
            return this;
        }

        this.worldTransform
            .copy(worldTransform)
            .multiply(this.getTransform());

        return this;
    }

    /**
     * @override
     */
    destroy() {
        super.destroy();

        if (this._localBounds) {
            this._localBounds.destroy();
            this._localBounds = null;
        }

        if (this._bounds) {
            this._bounds.destroy();
            this._bounds = null;
        }

        this._worldTransform.destroy();
        this._worldTransform = null;

        this._size.destroy();
        this._size = null;

        this._visible = null;
    }

    /**
     * @private
     */
    _updateSize() {

    }
}



