import Renderable from './Renderable';
import { removeItems } from '../utils';
import Rectangle from '../core/shape/Rectangle';

/**
 * @class Container
 * @extends {Renderable}
 */
export default class Container extends Renderable {

    /**
     * @constructor
     */
    constructor() {
        super();

        /**
         * @private
         * @member {Renderable[]}
         */
        this._children = [];
    }

    /**
     * @public
     * @readonly
     * @member {Renderable[]}
     */
    get children() {
        return this._children;
    }

    /**
     * @public
     * @chainable
     * @param {Renderable} child
     * @returns {Container}
     */
    addChild(child) {
        return this.addChildAt(child, this._children.length);
    }

    /**
     * @public
     * @chainable
     * @param {Renderable} child
     * @param {Number} index
     * @returns {Container}
     */
    addChildAt(child, index) {
        if (index < 0 || index > this._children.length) {
            throw new Error(`The index ${index} is out of bounds ${this._children.length}`);
        }

        if (child === this) {
            return this;
        }

        this._children.splice(index, 0, child);

        return this;
    }

    /**
     * @public
     * @chainable
     * @param {Renderable} firstChild
     * @param {Renderable} secondChild
     * @returns {Container}
     */
    swapChildren(firstChild, secondChild) {
        if (firstChild !== secondChild) {
            this._children[this.getChildIndex(firstChild)] = secondChild;
            this._children[this.getChildIndex(secondChild)] = firstChild;
        }

        return this;
    }

    /**
     * @public
     * @param {Renderable} child
     * @returns {Number}
     */
    getChildIndex(child) {
        const index = this._children.indexOf(child);

        if (index === -1) {
            throw new Error('Renderable is not a child of the container.');
        }

        return index;
    }

    /**
     * @public
     * @chainable
     * @param {Renderable} child
     * @param {Number} index
     * @returns {Container}
     */
    setChildIndex(child, index) {
        if (index < 0 || index >= this._children.length) {
            throw new Error(`The index ${index} is out of bounds ${this._children.length}`);
        }

        removeItems(this._children, this.getChildIndex(child), 1);
        this._children.splice(index, 0, child);

        return this;
    }

    /**
     * @public
     * @param {Number} index
     * @returns {Renderable}
     */
    getChildAt(index) {
        if (index < 0 || index >= this._children.length) {
            throw new Error(`getChildAt: Index (${index}) does not exist.`);
        }

        return this._children[index];
    }

    /**
     * @public
     * @chainable
     * @param {Renderable} child
     * @returns {Container}
     */
    removeChild(child) {
        const index = this._children.indexOf(child);

        if (index !== -1) {
            removeItems(this._children, index, 1);
        }

        return this;
    }

    /**
     * @public
     * @chainable
     * @param {Number} index
     * @returns {Container}
     */
    removeChildAt(index) {
        removeItems(this._children, index, 1);

        return this;
    }

    /**
     * @public
     * @chainable
     * @param {Number} begin
     * @param {Number} end
     * @returns {Container}
     */
    removeChildren(begin = 0, end = this._children.length) {
        const range = (end - begin);

        if (range < 0 || range > end) {
            throw new Error('removeChildren: numeric values are outside the acceptable range.');
        }

        if (range || this._children.length) {
            this._children.splice(begin, range);
        }

        return this;
    }

    /**
     * @override
     */
    render(displayManager, worldTransform) {
        if (!this.visible) {
            return this;
        }

        const transform = this.worldTransform
            .copy(worldTransform)
            .multiply(this.getTransform());

        for (const child of this._children) {
            child.render(displayManager, transform);
        }

        return this;
    }

    /**
     * @override
     */
    getBounds() {
        if (!this._bounds) {
            this._bounds = new Rectangle();
        }

        const bounds = this._bounds.copy(this.getLocalBounds());

        let minX = bounds.x,
            minY = bounds.y,
            maxX = bounds.width,
            maxY = bounds.height;

        for (const child of this._children) {
            if (!child.visible) {
                continue;
            }

            const childBounds = child.getBounds();

            minX = Math.min(minX, childBounds.x);
            minY = Math.min(minY, childBounds.y);
            maxX = Math.min(maxX, childBounds.width);
            maxY = Math.min(maxY, childBounds.height);
        }

        return bounds.set(minX, minY, maxX, maxY);
    }

    /**
     * @override
     */
    destroy() {
        super.destroy();

        this._children.length = 0;
        this._children = null;
    }
}



